import axios from 'axios';

const urrencyConverter = axios.create({
  baseURL: 'https://api.getgeoapi.com',
});

urrencyConverter.interceptors.response.use(function (response) {
  return response.data;
});

const headerCurrency = axios.create({
  baseURL: 'https://api.privatbank.ua',
});

headerCurrency.interceptors.response.use(function (response) {
  return response.data;
});

export { urrencyConverter, headerCurrency };
